<template>
  <section class="search">
    <div class="page-container">
      <h2 class="section-title h-decoration h-center"><span>Поиск рейсов</span></h2>
      <main class="dark">
        <form class="col align-items-center" action="search.html" method="GET" @submit.prevent="search">
          <div class="row form-fields">
            <div class="col">
              <label>От куда
                <select :class="[is_wrong_from && 'wrong']" id="from" name="from" v-model="from">
                  <option value="moscow">Москва</option>
                  <option value="kazan">Казань</option>
                </select>
              </label>
              <label>Куда
                <select :class="[is_wrong_to && 'wrong']" id="from" name="to" v-model="to">
                  <option value="moscow">Москва</option>
                  <option value="kazan">Казань</option>
                </select>
              </label>
            </div>
            <div class="col">
              <label>Вылет
                <input :class="[is_wrong_date1 && 'wrong']" id="date-from" type="date" name="date1" v-model="date1">
              </label>
              <label>Прилет
                <input :class="[is_wrong_date2 && 'wrong']" id="date-to" type="date" name="date2" v-model="date2">
              </label>
              <label>Пассажиров
                <input :class="[is_wrong_passengers && 'wrong']" id="passengers" type="number" name="passengers" value="1" min="1" max="48" v-model="passengers">
              </label>
            </div>
          </div>
          <div class="row">
            <input type="submit" value="Найти">
          </div>
        </form>
      </main>
    </div>
  </section>
</template>




<script>
export default {





  /**
   * ==========
   * Данные
   * ==========
   */
  data: () => ({
    from: '',
    to: '',
    date1: new Date().toISOString().split('T')[0],
    date2: '',
    passengers: 1,
    is_wrong_passengers: false,
    is_wrong_date1: false,
    is_wrong_date2: false,
    is_wrong_from: false,
    is_wrong_to: false,
  }),





  /**
   * ==========
   * Слежка
   * ==========
   */
  watch: {

    /**
     * Количество пасасжиров
     */
    passengers: function (val) {
      this.is_wrong_passengers = (val < 1 || val > 48) ? true : false
    },

    /**
     * Дата вылета
     */
    date1: function (val) {
      this.is_wrong_date1 = (new Date(val) < new Date()) ? true : false
      this.is_wrong_date2 = (new Date(this.date2) < new Date(val)) ? true : false
    },

    /**
     * Дата прилета
     */
    date2: function (val) {
      this.is_wrong_date2 = (new Date(val) < new Date(this.date1)) ? true : false
    },

    /**
     * От куда
     */
    from: function (val) {
      this.is_wrong_from = false
    },

    /**
     * Куда
     */
    to: function (val) {
      this.is_wrong_to = false
    },
  },





  /**
   * ==========
   * Методы
   * ==========
   */
  methods: {



    /**
     * Поиск полетов
     */
    async search() {
      /**
       * Получим iata коды аэропортов
       */
      let from = await this.getAirport(this.from)
      if (from.error) this.is_wrong_from = true
      else {
        this.is_wrong_from = false
        from  = from.data.items[0].iata
      }

      let to = await this.getAirport(this.to)
      if (to.error) this.is_wrong_to = true
      else {
        this.is_wrong_to = false
        to  = to.data.items[0].iata
      }

      /** Если город туда или обратно не установлен */
      if (this.is_wrong_from || this.is_wrong_to) return

      /**
       * Сформируем строку запроса к api
       */
      let query = `?from=${from}&to=${to}&date1=${this.date1}${this.date2 ? `&date2=${this.date2}` : ''}&passengers=${this.passengers}`
      let new_route = `/search${query}`

      /**
       * Если мы сейчас не на странице поиска рейсов, тогда изменить маршрут на нее
       */
      if (this.$route.path !== '/search') this.$router.push(new_route)
      /** Иначе вызвать событие для вызова api */
      else this.$emit('search', query)
    },



    /**
     * Поиск аэропорта
     */
    async getAirport(name) {
      let response = await fetch(`http://ws-module-2.loc/api/airport?query=${name}`)

      return await response.json()
    },
  },
}
</script>




<style scoped>
  .wrong {
    border-color: red !important;
  }
</style>
